import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

// styles
import GlobalStyles from "./assets/styles/global";
import "./App.css";


// components
import Header from "./Components/Header/header";
import Intro from "./Components/Intro/intro";
import About from "./Components/About/about";
import Products from "./Components/Products/products";
import Tasks from "./Components/Tasks/tasks";
import Footer from "./Components/Footer/footer";
import Work from "./Components/Work/work";
import Partners from "./Components/partners/partners";
import Posts from "./Components/Posts/posts";
import SocialMedia from "./Components/SocialMedia/socialmedia";
const Privacy = React.lazy(() => import("./Components/Privacy"));
const Terms = React.lazy(() => import("./Components/Terms"));
const UserAgreement = React.lazy(() => import("./Components/UserAgreement"));
const AboutUs = React.lazy(() => import("./Components/AboutUs/AboutUs"));

const App = () => {
  // Scroll Listener
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = (event) => {
      setScrollY(window.scrollY);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    onSubscribe();

    return () => offSubscribe();
  }, []);

  // Resize Listener
  const breakpoint = 991;

  const [widthWindow, setWidthWindow] = useState(window.screen.width);

  const onSubscribe = () => window.addEventListener("resize", windowResize);
  const offSubscribe = () => window.removeEventListener("resize", windowResize);

  const windowResize = () => {
    setWidthWindow(window.innerWidth);
  };

  useEffect(() => {
    if (widthWindow <= breakpoint) {
      setIsMobile(true);
    } else {
      setIsMobile(false);
    }
  }, [widthWindow]);

  // Localization change

  const [language, setLanguage] = useState(
    localStorage.getItem("Language") ? localStorage.getItem("Language") : "Ru"
  );

  return (
    <Router basename="/">
      <React.Suspense>
        <Routes>
          <Route
            path="/"
            element={
              <>
                <Header
                  changeLng={setLanguage}
                  language={language}
                  isMobile={isMobile}
                  scrollY={scrollY}
                />
                <Intro language={language} isMobile={isMobile} />
                <About language={language} isMobile={isMobile} />
                <Products language={language} isMobile={isMobile} />
                <Tasks language={language} isMobile={isMobile} />
                <Work language={language} isMobile={isMobile} />
                <Partners language={language} isMobile={isMobile} />
                <Posts language={language} isMobile={isMobile} />
                <SocialMedia language={language} isMobile={isMobile} />
                <Footer language={language} isMobile={isMobile} />
              </>
            }
          />

          <Route
            path="/about"
            element={
              <>
                <Header
                  language={language}
                  isMobile={isMobile}
                  scrollY={scrollY}
                />
                <AboutUs language={language} isMobile={isMobile} exact />
              </>
            }
          />
          <Route path="/privacy" element={<Privacy />} exact />
          <Route path="/terms" element={<Terms />} exact />
          <Route path="/u_agreement" element={<UserAgreement />} exact />
        </Routes>

        <GlobalStyles />
      </React.Suspense>
    </Router>
  );
};

export default App;
