import React from "react";

const FooterDesktopLazy = React.lazy(() => import("./footerDesktop"));
const FooterMobileLazy = React.lazy(() => import("./footerMobile"));

const Footer = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <FooterMobileLazy language={language} />
      ) : (
        <FooterDesktopLazy language={language} />
      )}
    </>
  );
};

export default Footer;
