import React from "react";

const TasksDesktopLazy = React.lazy(() => import("./tasksDesktop"));
const TasksMobileLazy = React.lazy(() => import("./tasksMobile"));

const Tasks = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <TasksMobileLazy language={language} />
      ) : (
        <TasksDesktopLazy language={language} />
      )}
    </>
  );
};

export default Tasks;
