import { createGlobalStyle } from 'styled-components'

export default createGlobalStyle`

  html {
    scroll-behavior: smooth;
    overflow-x: hidden;
  }

  * {
    margin: 0;
    padding: 0;
    -webkit-tap-highlight-color: rgba(0,0,0,0.05);
  }

  body {
    width: 100%;
    overflow-x: hidden;
  }

  ul,ol {
    list-style: none;
  }

  a {
    text-decoration: none;
  }

`