import React from "react";

const SocialMediaDesktopLazy = React.lazy(() => import("./socialmediaDesktop"));
const SocialMediaMobileLazy = React.lazy(() => import("./socialmediaMobile"));

const SocialMedia = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <SocialMediaMobileLazy language={language} />
      ) : (
        <SocialMediaDesktopLazy language={language} />
      )}
    </>
  );
};

export default SocialMedia;
