import React from "react";

const PostsDesktopLazy = React.lazy(() => import("./postsDesktop"));
const PostsMobileLazy = React.lazy(() => import("./postsMobile"));

const Posts = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <PostsMobileLazy language={language} />
      ) : (
        <PostsDesktopLazy language={language} />
      )}
    </>
  );
};

export default Posts;
