import React from "react";

const WorkDesktopLazy = React.lazy(() => import("./workDesktop"));
const WorkMobileLazy = React.lazy(() => import("./workMobile"));

const Work = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <WorkMobileLazy language={language} />
      ) : (
        <WorkDesktopLazy language={language} />
      )}
    </>
  );
};

export default Work;
