import React from "react";

const AboutDesktopLazy = React.lazy(() => import("./aboutDesktop"));
const AboutMobileLazy = React.lazy(() => import("./aboutMobile"));

const About = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <AboutMobileLazy language={language} />
      ) : (
        <AboutDesktopLazy language={language} />
      )}
    </>
  );
};

export default About;
