import React from "react";

const ProductsDesktopLazy = React.lazy(() => import("./productsDesktop"));
const ProductsMobileLazy = React.lazy(() => import("./productsMobile"));

const Products = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <ProductsMobileLazy language={language} />
      ) : (
        <ProductsDesktopLazy language={language} />
      )}
    </>
  );
};

export default Products;
