import React from "react";

const IntroDesktopLazy = React.lazy(() => import("./introDesktop"));
const IntroMobileLazy = React.lazy(() => import("./introMobile"));

const RunLineDesktopLazy = React.lazy(() => import("./RunLineDesktop"));
const RunLineMobileLazy = React.lazy(() => import("./RunLineMobile"));

const Intro = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <>
          <IntroMobileLazy language={language} />
          <RunLineMobileLazy />
        </>
      ) : (
        <>
          <IntroDesktopLazy language={language} />
          <RunLineDesktopLazy />
        </>
      )}
    </>
  );
};

export default Intro;
