import React from "react";

const PartnersDesktopLazy = React.lazy(() => import("./partnersDesktop"));
const PartnersMobileLazy = React.lazy(() => import("./partnersMobile"));

const Partners = ({ isMobile, language }) => {
  return (
    <>
      {isMobile ? (
        <PartnersMobileLazy language={language} />
      ) : (
        <PartnersDesktopLazy language={language} />
      )}
    </>
  );
};

export default Partners;
